*
{
  padding: 0;
  margin: 0;
}

:root
{
  --black: #1D242B;
  --secondary-black: #283739;
  --white: #FAFAFA;
  --blue: #0077C0;
}

html, body
{
  background: var(--black);
  font-family: sans-serif;
}

header
{
  background: var(--blue);
  height: 60px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo
{
  height: 60px;
  width: 60px;
  margin: 0 10px;
  align-items: center;
  display: flex;
}

.boxes-container
{
  width: auto;
  display: flex;
}

.add-bill-btn-container
{
  position: fixed;
  bottom: 1%;
  right: 1%;
}

.creator-sign
{
  height: 20px;
  width: 140px;
  color: #FAFAFA;
  background-color: #1D242B;
  display: flex;
  justify-content: center;
  position: fixed;
  bottom: 0;
  left: 15px;
  padding: 5px;
  border: 1px solid #FAFAFA;
  border-radius: 5px 5px 0 0 ;
  text-decoration: none;
  cursor: pointer;
}